import React from 'react'
import ReactDOM from 'react-dom'

import { createStore, combineReducers, applyMiddleware } from 'redux'
import { Provider } from 'react-redux'


import { Route,Router,browserHistory,IndexRoute } from 'react-router'

import { syncHistoryWithStore } from 'react-router-redux'

import thunk from 'redux-thunk'

import app from '../reducers' // Or wherever you keep your reducers





const stores = applyMiddleware(thunk)(createStore)

const store  = stores(app)

// Add the reducer to your store on the `router` key
// Also apply our middleware for navigating
const historys = syncHistoryWithStore(browserHistory,store)

import List from '../containers/list';
import Content from '../containers/content';
// Now you can dispatch navigation actions from anywhere!


 ReactDOM.render(

     <Provider store={store}>
        <Router history={historys}>
                <Route   path="/" component={List}/>
                <Route  path="/content" component={Content}/>
        </Router>
     </Provider>
    ,
    document.getElementById('root')
)




